<template>
  <v-content>
    <editOwner :show="editOwner" :owner="editOwnerData" @closed="closeEditOwner"></editOwner>
    <v-row align="center">
      <v-col style="padding:12px 0px 5px 20px" cols="auto">
        <v-avatar size="80">
          <img :src="user.avatar" />
        </v-avatar>
      </v-col>
      <v-col style="padding:0px 3px 0px 1%" cols="5">
        <v-row align="center" justify="center" no-gutters>
          <h1>{{$t("titleAdmin")}}!</h1>

          <v-col style="padding:0 0 0 5%">
            <v-row align="center" justify="start">
              <v-col cols="2">
                <settings></settings>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-data-table
      :loading="loading"
      loading-text="Loading... Please wait"
      :headers="headers"
      :items="owners"
      :search="search"
      style="margin:0 10% 0 10%"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-dialog v-model="ownerSignUp" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-on="on" v-bind="attrs">Create Owner</v-btn>
            </template>
            <addOwner :show="ownerSignUp" @closed="closeOwnerSignUp"></addOwner>
          </v-dialog>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-toolbar>
      </template>

      <template v-slot:item.active="{ item }">
        <v-simple-checkbox v-model="item.active" disabled></v-simple-checkbox>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon medium class="mr-2" @click="openEditOwner(item)">mdi-pencil</v-icon>
      </template>
    </v-data-table>
  </v-content>
</template>

<script>
import addOwner from "../components/addOwner";
import { mapGetters } from "vuex";
import editOwner from "../components/editOwner";
import settings from "../components/settingsAdmin";

export default {
  name: "Admin",
  components: {
    addOwner,
    editOwner,
    settings
  },
  data() {
    return {
      ownerSignUp: false,
      headers: [
        { text: "Name", value: "firstName", align: "center" },
        { text: "Last Name", value: "lastName", align: "center" },
        { text: "Phone", value: "phone", align: "center" },
        { text: "email", value: "email", align: "center" },
        { text: "Clients", value: "clients", align: "center" },
        { text: "Trainers", value: "trainers", align: "center" },
        { text: "Active", value: "active", align: "center" },
        { text: "Actions", value: "actions", align: "center" }
      ],
      search: "",
      editOwner: false,
      editOwnerData: {}
    };
  },
  created() {
    this.loading = true;
    this.$store.dispatch("getOwners");
    this.loading = false;
  },
  computed: mapGetters(["owners", "user"]),
  methods: {
    openEditOwner(owner) {
      this.editOwnerData = owner;
      this.editOwner = true;
    },
    closeEditOwner() {
      this.editOwner = false;
    },
    closeOwnerSignUp() {
      this.ownerSignUp = false;
    }
  }
};
</script>
