<template>
  <v-content>
    <v-dialog eager :value="show" persistent width="600">
      <!-- <successAlert
        :show="createClientNotification"
        :text="alertText"
        @closed="createClientNotification = false"
      ></successAlert>-->
      <v-dialog v-model="loading" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text class="center">
            Creating Owner
            <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-sheet>
        <v-form ref="form" v-model="valid">
          <v-row justify="center" style="margin:0">
            <h1>{{$t("addAndEditClient.titleAdd")}}</h1>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.firstName")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="owner.firstName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.lastName")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="owner.lastName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.email")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="owner.email"
                :full-width="true"
                filled
                disabled
                :error-messages="wrongEmail"
                :rules="[rules.required,rules.email]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Phone</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="owner.phone"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Clients</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model.number="owner.clients"
                min="0"
                :rules="[rules.required]"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Trainers</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model.number="owner.trainers"
                min="0"
                :rules="[rules.required]"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.active")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox v-model="owner.active" style="padding:0 0 25px 40%;height:10px;margin:0"></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
            <v-btn @click="close" color="error" :normal="true">{{$t("addAndEditClient.close")}}</v-btn>
            <v-btn
              color="primary"
              @click="createOwner"
              :normal="true"
            >{{$t("addAndEditClient.create")}}</v-btn>
          </v-row>
        </v-form>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
// import moment from "moment";
// import successAlert from "./successAlert";

export default {
  name: "EditOwner",
  // components: { successAlert },
  props: ["show", "owner"],
  data() {
    return {
      menu2: false,
      valid: false,
      loading: false,
      showPassword: false,
      createClientNotification: false,
      timeout: 3000,
      alertText: "Client created",
      rules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 8 || "Min 8 characters",
        email: v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      }
    };
  },
  computed: {
    ...mapGetters(["types", "values", "user", "trainers", "wrongEmailCreate"]),
    trainersArray: function() {
      let array = this.trainers.filter(trainer => {
        return trainer.active === true;
      });
      return array;
    },
    wrongEmail() {
      const errors = [];
      if (this.wrongEmailCreate) {
        this.stopLoading();
        errors.push("This email is in use");

        return errors;
      } else {
        return [];
      }
    }
  },
  watch: {
    show: function() {
      this.$store.commit("wrongEmailCreate", false);
    }
  },
  methods: {
    stopLoading: function() {
      this.loading = false;
    },
    close: function() {
      this.client = {};
      this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    createOwner: async function() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        await this.$store.dispatch("updateOwner", this.owner);
        await this.$store.dispatch("getOwners");
        this.$store.commit("wrongEmailCreate", false);
        this.loading = false;
        // this.createClientNotification = true;

        this.$refs.form.resetValidation();
      }
    }
  }
};
</script>

<style scoped>
.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>
